          {prop: 'detail', label: '', width: '200', align: 'left'},
          {prop: 'coin', label: '', align: 'right',width: '96'}
        ],
        tableData: [],
        page: {
          pageNo: 1,
          limit: 10,
          sizes: [10, 50, 100],
