      },
      // 
      handleSizeChange(val) {

      },
      // 
      handleCurrentChange(val) {

      },
      editTableData(row) {
        console.log('row',row);
      },
      //
      goAddAds(){
        this.$router.push({
          name:'AddType',
        })
      },
      //
      pSubmit(row) {

      },
      //
      editPop(id){
        if(id === 1){
          this.routerName = 'InfoSet'
        }else if(id === 2){
          this.routerName = 'Cash'
        }else if(id === 3){
          this.routerName = 'Watch'
        }else if(id === 4){
          this.routerName = 'AlreadyNew'
        }else if(id === 5){
          this.routerName = 'OnlineTime'
        }
        this.$router.push({
          name:this.routerName
        })
      }
    }

  }
</script>

<style lang="css" scoped>
  .searchItem{
    margin-right: 32px;
  }
  .searchInput{
    width: 168px;
  }
  .searchSelect{
    width: 136px;
  }
  .searchCoBtn{
    margin-left: -20px;
  }
</style>



